import React from 'react';
import history from '../../history';
import NavBar from "../utils/navbar";
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';


const useStyles = makeStyles((theme) => ({

    root:{
        backgroundImage: "url('/imagens/montanha.jpg')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        width: "100%",
        height: "100%",
        minWidth: "70em",
        minHeight: "60.55em",
        position: "absolute",
    },
    button:{
        backgroundColor: "#555555", 
        border: "none",
        color: "rgb(248, 248, 248)",
        padding: "15px 32px",
        textAlign: "center",
        textDecoration: "none",
        display: "inline-block",
        fontSize: "18px",
        width: "200px",
    },
    h:{
        textAlign: "center",
        fontSize: "xx-large",
        marginTop: "30%",
        color: "white",

    }

}));


export default function Quizz(){

    const classes = useStyles();

    function gotoAppStartQuizz(){
        history.push("/quizz/start");
    }

    function gotoAppPostQuizz(){
        history.push("/quizz/create");
    }

        return(
            <div className={classes.root}>
            <NavBar></NavBar>
            <Grid container>
                <Grid item xs={5}>
                    <h2 className={classes.h}>Quizz</h2>
                    <Grid container spacing={3}  
                        direction="column"
                        justify="center"
                        alignItems="center"> 
                        <Grid item xs={6}>
                            <input type="submit" className={classes.button} onClick ={gotoAppStartQuizz} value = "Start Quizz" data-test = "submit"/>
                        </Grid>
                        <Grid item xs={6}>        
                            <input type="submit" className={classes.button} onClick ={gotoAppPostQuizz} value = "Post Quizz" data-test = "submit"/>              
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={7}>
                    
                </Grid>
            </Grid>
            </div>
        );
}